                    'lengthFilter': encodeURIComponent(self.lengthChecked),
                    'bidPriceFilter': encodeURIComponent(self.bidPriceChecked),
                    'sortDict': JSON.stringify(self.sortDict)
                }
                //    console.log(Params)
                self.fetchItems(Params)
            } //else {
            //   self.$Message.info('');
            //}
